function i80f48FromNumber(x: number): anchor.BN {
  let MULTIPLIER_NUMBER = Math.pow(2, 48);
  let int_part = Math.trunc(x);
  let v = new anchor.BN(int_part).iushln(48);
  v.iadd(new anchor.BN((x - int_part) * MULTIPLIER_NUMBER));
  return v;
}
